<!-- *************************************************************************
     TEMPLATE
     ************************************************************************* -->

<template lang="pug">
div(
  :class=`[
    "c-twitter-tweet-box",
    "c-twitter-tweet-box--" + size,
    "c-twitter-tweet-box--" + theme,
    {
      "c-twitter-tweet-box--loading": loading
    }
  ]`
)
  .c-twitter-tweet-box__header
    img(
      v-if="twitterAvatar"
      @click="onGoToTwitter"
      :src="twitterAvatar"
      class="c-twitter-tweet-box__avatar"
    )

    twitter-logo(
      v-else
      @click="onGoToTwitter"
      class="c-twitter-tweet-box__avatar"
    )

    .c-twitter-tweet-box__information
      span(
        @click="onGoToTwitter"
        class="c-twitter-tweet-box__name"
      ) {{ twitterName || twitterUsername }}

      button(
        @click="onGoToTwitter"
        class="c-twitter-tweet-box__button"
      ) {{ buttonContent }}

  div(
    v-if="twitterTweetWithoutScript"
    class="c-twitter-tweet-box__body"
  )
    base-spinner(
      v-if="loading"
      :size="size"
      class="c-twitter-tweet-box__spinner"
    )

    div(
      v-show="!loading"
      v-html="twitterTweetWithoutScript"
      class="c-twitter-tweet-box__tweet"
    )
</template>

<!-- *************************************************************************
     SCRIPT
     ************************************************************************* -->

<script>
// NPM
import { BaseSpinner } from "@growthbunker/vuedarkmode"

// PROJECT: COMPONENTS
import TwitterLogo from "@/components/logos/TwitterLogo"

// PROJECT: HELPERS
import { loadScript } from "@/helpers/widgets"

// PROJECT: MIXINS
import TwitterTweetMixin from "@/mixins/widgets/TwitterTweetMixin"

export default {
  components: {
    BaseSpinner,
    TwitterLogo
  },

  mixins: [TwitterTweetMixin],

  data: () => ({
    // --> STATE <--

    loading: false
  }),

  computed: {
    twitterTweetWithoutScript() {
      return (this.twitterTweet || "").replace(/<script(.*?)<\/script>/gi, "")
    }
  },

  mounted() {
    this.insertTwitterScript()
  },

  methods: {
    // --> HELPERS <--

    async insertTwitterScript() {
      if (!window.twttr) {
        await loadScript("https://platform.twitter.com/widgets.js")

        this.loadTweet()
      } else {
        this.loadTweet()
      }
    },

    loadTweet() {
      this.loading = true

      try {
        window.twttr.widgets.load()
      } catch (error) {
        console.warn(error)
      }

      // Hide spinner when widget is loaded
      window.twttr.events.bind("rendered", () => {
        this.loading = false
      })
    },

    // --> EVENT LISTENERS <--

    onGoToTwitter() {
      window.open(`https://www.twitter.com/${this.twitterUsername}`, "_blank")
    }
  }
}
</script>

<!-- *************************************************************************
     STYLE
     ************************************************************************* -->

<style lang="scss">
// VARIABLES
$c: ".c-twitter-tweet-box";
$sizes: "mini", "small", "default", "medium", "large";

#{$c} {
  #{$c}__header {
    display: flex;
    border-bottom-width: 1px;
    border-bottom-style: solid;

    #{$c}__avatar {
      margin-top: 2px;
      flex: 0 0 auto;
      border-radius: 100%;
      cursor: pointer;
    }

    #{$c}__information {
      display: flex;
      align-items: flex-start;
      flex: 1;
      flex-direction: column;

      #{$c}__name {
        font-weight: 800;
        cursor: pointer;

        &:hover {
          text-decoration: underline;
        }
      }

      #{$c}__button {
        position: relative;
        display: inline-block;
        overflow: hidden;
        min-width: 60px;
        outline: none;
        border: 0;
        border: 1px solid #1da1f2;
        border-radius: 25px;
        background-color: transparent;
        color: #1da1f2;
        vertical-align: middle;
        text-align: center;
        white-space: nowrap;
        font-weight: 800;
        transition: all 125ms linear;
        user-select: none;
        cursor: pointer;

        @include no-tap-highlight-color;

        &:hover {
          background-color: rgba(#1da1f2, 0.1);
        }
      }
    }
  }

  #{$c}__body {
    #{$c}__tweet {
      display: flex;
      justify-content: center;

      .twitter-tweet {
        flex: 0 0 auto;
        margin: 0 !important;
      }
    }
  }

  // --> SIZES <--

  @each $size in $sizes {
    $i: index($sizes, $size) - 1;

    &--#{$size} {
      #{$c}__header {
        padding: 10px + (1px * $i);

        #{$c}__avatar {
          margin-right: 10px + (1px * $i);
          width: 40px + (5px * $i);
          height: 40px + (5px * $i);
        }

        #{$c}__information {
          #{$c}__name {
            margin-bottom: 6px + (1px * $i);
            font-size: 12px + (1px * $i);
            line-height: 12px + (1px * $i);
          }

          #{$c}__button {
            padding: (7px + (1px * $i)) (10px + (2px * $i));
            font-size: 10px + (1px * $i);
          }
        }
      }

      #{$c}__body {
        #{$c}__tweet {
          padding: 8px + (1px * $i);
        }
      }

      &#{$c}--loading {
        #{$c}__body {
          padding: 40px + (5px * $i) 0;
        }
      }
    }
  }

  // --> BOOLEANS <--

  &--loading {
    #{$c}__body {
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }

  // --> THEMES <--

  @each $theme in $themes {
    $themeName: map-get($theme, "name");

    &--#{$themeName} {
      #{$c}__header {
        border-bottom-color: mdg($theme, "borders", "default", "primary");
        background-color: mdg($theme, "backgrounds", "default", "secondary");
        box-shadow: 0 1px 3px mdg($theme, "shadows", "default", "primary");

        #{$c}__avatar {
          box-shadow: 0 1px 3px mdg($theme, "shadows", "default", "primary");
        }
      }
    }
  }
}
</style>
